import React from 'react';

import css from './Home.module.css';

function Home() {

    return (
        <section className={css.section}>
            <div className={css.prologue}>
                <h1>Aaron Chung <span>(Chen-Jui Chung)</span></h1>
                <h2>Fullstack Engineer + Team Lead</h2>
            </div>
            <div className={css.subscript}>
                Made with passion, coffee, and <a href="https://www.youtube.com/c/LofiGirl" target="_blank" rel="noreferrer">Lofi Girl</a>
            </div>
        </section>
        
    );
}

export default Home;