import React from 'react';
import { Link } from 'react-router-dom';

import css from './NotFound.module.css';

function NotFound() {

    return (
        <section>
            <div className={css.notFound}>
                <h1>404 - Resource Not Found</h1>
                <p>There's nothing I can share with you at this moment...how about heading back to <Link to="/" style={{color: '#cccccc', fontWeight: '700'}}>home page</Link>?</p>
            </div>
        </section>
        
    );
}

export default NotFound;