import React from 'react';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import AddLocationAltIcon from '@mui/icons-material/AddLocationAlt';
import WorkHistoryIcon from '@mui/icons-material/WorkHistory';
import LanguageIcon from '@mui/icons-material/Language';
import SchoolIcon from '@mui/icons-material/School';

import IntroCard from '../components/UI/Cards/IntroCard';

import css from './Intro.module.css';

import { location, works, languages, educations } from '../store/introductionList';



function Intro() {

    return (
        <section className={css.section}>
            <div >
                <h1 className={css.sectionTitle}>About Myself</h1>
            </div>
            <Container>
                <Row>
                    <Col lg={6}>
                        <IntroCard icon={<AddLocationAltIcon />} data={location} />
                    </Col>
                    <Col lg={6}>
                        <IntroCard icon={<WorkHistoryIcon />} data={works} />
                    </Col>
                </Row>
                <Row>
                    <Col lg={6}>
                        <IntroCard icon={<LanguageIcon />} data={languages} />
                    </Col>
                    <Col lg={6}>
                        <IntroCard icon={<SchoolIcon />} data={educations} />
                    </Col>
                </Row>
            </Container>
        </section>
    );
}

export default Intro;