import React from 'react';
import { NavLink } from 'react-router-dom';

import css from './Header.module.css';

function Header() {
    const [isPositionFixed, setIsPositionFixed] = React.useState<boolean>(false);

    const onScrollHandler = () => {
        const viewHeight = document.documentElement.clientHeight;
        const pageHeight = document.documentElement.scrollHeight;
        const currPosition = document.documentElement.scrollTop;
        const atPageBottom = viewHeight + currPosition >= pageHeight * 0.95; 
        
        if (atPageBottom && !isPositionFixed){
            setIsPositionFixed(true);
        } else if (!atPageBottom && isPositionFixed) {
            setIsPositionFixed(false);
        }
    };

    window.onscroll = onScrollHandler;

    return (
        <header className={`${css.header} ${isPositionFixed && css.headerFixed}`}>
            <nav>
                <NavLink to="/">Home</NavLink>
                <NavLink to="/skills">Skills</NavLink>
            </nav>
        </header>
    );
}

export default Header;