import React from 'react';
import { Link } from 'react-router-dom';


import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import SearchIcon from '@mui/icons-material/Search';
import RouteIcon from '@mui/icons-material/Route';
import PlayArrowOutlinedIcon from '@mui/icons-material/PlayArrowOutlined';
import GridViewOutlinedIcon from '@mui/icons-material/GridViewOutlined';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import SettingsIcon from '@mui/icons-material/Settings';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import HorizontalRuleIcon from '@mui/icons-material/HorizontalRule';
import CloseIcon from '@mui/icons-material/Close';
import DataObjectIcon from '@mui/icons-material/DataObject';

import css from './VSCode.module.css';

function Experiment() {
    return (
        <section className={css.section}>
            <div className={css.container}>
                <div className={css.menubar}>
                    <ul>
                        <li>File</li>
                        <li>Edit</li>
                        <li>Selection</li>
                        <li>View</li>
                        <li>Go</li>
                        <li>Run</li>
                        <li>Terminal</li>
                        <li>Help</li>
                    </ul>
                    <ul>
                        <li><HorizontalRuleIcon sx={{ width: '16px', height: '16px' }} /></li>
                        <li><ContentCopyIcon sx={{ width: '16px', height: '16px' }} /></li>
                        <li><CloseIcon sx={{ width: '16px', height: '16px' }} /></li>
                    </ul>
                </div>
                <div className={css.idemain}>
                    <div className={css.sidebar}>
                        <div>
                            <ul>
                                <li className={css.sidebarActive}><ContentCopyIcon /></li>
                                <li><SearchIcon /></li>
                                <li><RouteIcon /></li>
                                <li><PlayArrowOutlinedIcon /></li>
                                <li><GridViewOutlinedIcon /></li>
                            </ul>
                        </div>
                        <div>
                            <ul>
                                <li><AccountCircleIcon /></li>
                                <li><SettingsIcon /></li>
                            </ul>
                        </div>
                    </div>
                    <div className={css.explorer}>
                        <div className={css.explorerTitleBar}>
                            <span>EXPLORER</span>
                            <span><MoreHorizIcon /></span>
                        </div>
                        <div className={css.explorerMain}>
                            <div>
                                <div className={`${css.explorerListExpanded}`}>
                                    <span><KeyboardArrowDownIcon /></span>
                                    <span>WorkDir</span>
                                    <div className={`${css.explorerList} ${css.explorerListExpanded}`}>
                                        <span><KeyboardArrowDownIcon /></span>
                                        <span>acmitr.github.io</span>
                                        <div className={`${css.explorerList} ${css.explorerListCollapsed} ${css.explorerListInactive}`}>
                                            <span><KeyboardArrowRightIcon /></span>
                                            <span>node_modules</span>
                                        </div>
                                        <div className={`${css.explorerList} ${css.explorerListCollapsed}`}>
                                            <span><KeyboardArrowRightIcon /></span>
                                            <span>public</span>
                                        </div>
                                        <div className={`${css.explorerList} ${css.explorerListCollapsed}`}>
                                            <span><KeyboardArrowRightIcon /></span>
                                            <span>src</span>
                                        </div>
                                        <div className={`${css.explorerList} ${css.explorerListCollapsed}`}>
                                            <span><ContentCopyIcon sx={{ width: '16px' }} /></span>
                                            <span>.gitignore</span>
                                        </div>
                                        <div className={`${css.explorerList} ${css.explorerListCollapsed}`}>
                                            <span><ContentCopyIcon sx={{ width: '16px' }} /></span>
                                            <span>index.html</span>
                                        </div>
                                        <div className={`${css.explorerList} ${css.explorerListCollapsed}`}>
                                            <span><ContentCopyIcon sx={{ width: '16px' }} /></span>
                                            <span>Welcome.txt</span>
                                        </div>
                                        <div className={`${css.explorerList} ${css.explorerListCollapsed}`}>
                                            <span><DataObjectIcon sx={{ width: '16px' }} /></span>
                                            <span><Link to='./about' className={css.link}>package.json</Link></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className={css.workplace}>
                        <div className={css.tabBar}>
                            <div className={`${css.tab} ${css.tabActive}`}>
                                <span><ContentCopyIcon sx={{ width: '16px', height: '16px' }} /></span>
                                <span>Welcome.txt</span>
                            </div>
                            <div className={css.tab}>
                                <span><ContentCopyIcon sx={{ width: '16px', height: '16px' }} /></span>
                                <span>index.html</span>
                            </div>

                        </div>
                        <div className={css.editor}>
                            <div className={css.typewriterContainer}>
                                {/* &lt;h1&gt;Passionate in:&lt;/h1&gt;  */}
                                Enjoy in:
                                <div className={css.typewriter}>
                                    {/* &lt;strong&gt;brainstorming, programming, problem solving, and masterpiece delivery.&lt;/strong&gt; */}
                                    brainstorming, programming, problem solving, and masterpiece delivery.
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>


        </section>
    );
}

export default Experiment;