import React from "react";

import IntroType from "../../../models/IntroItem";

import css from './IntroCard.module.css';

function IntroCard(props: {icon: React.ReactNode, data: IntroType}) {
    return (
        <div className={css.card}>
            <div className={css.title}>
                <span className={css.icon}>{props.icon}</span>
                <span>{props.data.title}</span>
            </div>
            
            <div className={css.content}>
                {props.data.items.map((item, index) => 
                    <div key={index} className={css.li}>
                        <div className={css.liTitle}>{item.title}</div>
                        <div className={css.liDescription}>{item.description}</div>
                    </div>
                )}
            </div>
        </div>
    );
}

export default IntroCard;