import React from 'react';

import SearchContext from '../../../context/search-context';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import SearchIcon from '@mui/icons-material/Search';
import InputAdornment from '@mui/material/InputAdornment';
import { styled } from '@mui/material/styles';

const MuiStyledTextField = styled(TextField)({
    color: 'red',
    '& .MuiInputBase-input': {
        color: '#ffffff'
    },
    '& label': {
        color: '#cccccc'
    },
    '& label.Mui-focused': {
        color: '#cccccc',
    },
    '& .MuiInput-underline:after': {
        borderBottomColor: '#cccccc',
    },
});

function SearchInput(props: {id: string, label: string, embeddedAdornment: boolean}) {
    const searchRef = React.useRef<HTMLInputElement>();
    const searchContext = React.useContext(SearchContext);


    const searchOnChange = () => {
        searchContext.keywordOnChange(searchRef.current!.value);
    };
    
    if (!!props.embeddedAdornment){
        return (
            <Box sx={{ display: 'flex', alignItems: 'flex-end'}}>
            <MuiStyledTextField 
                id={props.id} 
                variant="standard" 
                label={props.label} 
                onChange={searchOnChange} 
                inputRef={searchRef} 
                inputProps={{maxLength: '16'}} 
                InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <SearchIcon sx={{ color: '#cccccc', mr: 1, my: 0.5 }} />
                      </InputAdornment>
                    ),
                  }}
                value={searchContext.keyword} />
        </Box>
        );
    } else {
        return (
            <Box sx={{ display: 'flex', alignItems: 'flex-end'}}>
                <SearchIcon sx={{ color: '#cccccc', mr: 1, my: 0.5 }} />
                <MuiStyledTextField 
                    id={props.id} 
                    variant="standard" 
                    label={props.label} 
                    onChange={searchOnChange} 
                    inputRef={searchRef} 
                    inputProps={{maxLength: '16'}} 
                    value={searchContext.keyword} />
            </Box>
        );
    }
}

export default SearchInput;