import React from 'react';

import SearchInput from '../components/UI/Inputs/SearchInput';
import SkillSection from '../components/UI/SkillSection';
import SearchContext from '../context/search-context';

import css from './Skills.module.css';

import SkillItemType from '../models/SkillItem';
import { webApp, swe, itManagement, projects, clouds } from '../store/skillList';

const filterSkillList = (skillList: SkillItemType[], keyword: string) => {
    keyword = keyword.trim().toLowerCase();

    if (keyword === ''){
        return skillList;
    }

    return skillList.filter(s => s.name.toLowerCase().includes(keyword) || s.tag.toLowerCase().includes(keyword) || s.content.toLowerCase().includes(keyword));
}

function Skills() {
    const searchContext = React.useContext(SearchContext);
    
    const filteredWebAppStack = filterSkillList(webApp, searchContext.keyword);
    const filteredSwEngineering = filterSkillList(swe, searchContext.keyword);
    const filteredItManagement = filterSkillList(itManagement, searchContext.keyword);
    const filteredProjects = filterSkillList(projects, searchContext.keyword);
    const filteredCloudPlatform = filterSkillList(clouds, searchContext.keyword);

    return (
        <section className={css.section}>
            <div className={css.skills}>
                <div className={css.header}>
                    <h1>Skills</h1>
                    <SearchInput id="searchSkill" label="Search keyword" embeddedAdornment={false} />
                </div>

                <SkillSection title="Web Application Stack" cards={filteredWebAppStack} />
                <SkillSection title="Software Engineering" cards={filteredSwEngineering} />
                <SkillSection title="Project Management" cards={filteredProjects} />
                <SkillSection title="IT Management" cards={filteredItManagement} />
                <SkillSection title="Cloud" cards={filteredCloudPlatform} />
                
            </div>
        </section>
    );
}

export default Skills;