import React from 'react';

import css from './Roles.module.css';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import RoleCard from '../components/UI/Cards/RoleCard';

import { junior, swe, lead} from '../store/roleList';

function Roles() {

    return (
        <section className={css.section}>
            <h1 className={css.sectionTitle}>My Role Lattice</h1>
            <Container>
                <Row>
                    <Col xl="4" lg="6" className={css.cardColumn}>
                        <RoleCard roleData={junior} disabled={true} disableMsg="Time never goes backward" />
                    </Col>
                    <Col xl="4" lg="6" className={css.cardColumn}>
                        <RoleCard roleData={swe} linkTo='./skills' />
                    </Col>
                    <Col xl="4" lg="12" className={css.cardColumn}>
                        <RoleCard roleData={lead} linkTo='./skills' />
                    </Col>
                </Row>
            </Container>
        </section>
    );
}

export default Roles;