import React from "react";

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import SkillCard from "./Cards/SkillCard";

import css from './SkillSection.module.css';
import SkillItemType from "../../models/SkillItem";

function SkillSection(props: {title: string, cards: SkillItemType[]}) {
    return (
        <section className={css.section}>
            <div className={css.title}>{props.title}</div>

            
            {props.cards.length > 0 && 
                <Container style={{marginLeft: 'auto', marginRight: 'auto', paddingLeft: 0, paddingRight: 0}}>
                    <Row>
                        {props.cards.map((card, index) => <Col xl={3} lg={4} md={6} key={index} ><SkillCard skill={card} /></Col>)}
                    </Row>
                </Container>
            }
            {props.cards.length === 0 && <p className={css.emptyMsg}>Oops...nothing to be listed :(</p>}
        </section>
    );
}

export default SkillSection;