import React from 'react';

import DemoCarousel, { CarouselItem } from '../components/UI/Carousels/DemoCarousel';

import css from './Demo.module.css';

const Demo = () => {
    const demo1: CarouselItem[] = [{
        image: { src: '/images/sch-acdemoapp01/demo-sch-snapshot1.jpg', alt: 'screen shot' }
    }, {
        image: { src: '/images/sch-acdemoapp01/demo-sch-snapshot2.jpg', alt: 'screen shot' }
    }, {
        image: { src: '/images/sch-acdemoapp01/demo-sch-snapshot3.jpg', alt: 'screen shot' }
    }];


    return (
        <section className={css.section}>
            <h1 className={css.sectionTitle}>Demonstration</h1>
            <h2 className={css.sectionSubscript}>Scrum Cloud Hub - Manage Scrum projects for organizations</h2>
            <DemoCarousel id='demo1-carousel' items={demo1} />
            <div className={css.details}>
                <div>Frontend: React 18 + Typescript 4.7 + bootstrap + MUI 5 + Redux Toolkit...</div>
                <div>Backend: Firebase Authentication + Realtime Database</div>
                
            </div>
        </section>
    );
};

export default Demo;