import React from 'react';

import { dependencies, others } from '../store/aboutList';

import css from './About.module.css';

function About() {
    return (
        <section className={css.section}>
            <h1>Dependencies Used</h1>
            <ul className={css.list}>
                {dependencies.map((d, index: number) => {
                    return (
                        <li key={index}>
                            <span className={css.title}>{d.title}</span>
                            <span className={css.description}>{d.description}</span>
                        </li>
                    );
                })}
            </ul>
            <h1>Others</h1>
            <ul className={css.list}>
                {others.map((d, index: number) => {
                    return (
                        <li key={index}>
                            <span className={css.title}>{d.title}</span>
                            <span className={css.description}>{d.description}</span>
                        </li>
                    );
                })}
            </ul>
        </section>
    );
}

export default About;