import React from 'react';

import LinkedInIcon from '@mui/icons-material/LinkedIn';
import EmailIcon from '@mui/icons-material/Email';

import css from './Contact.module.css';

function Contact() {
    return (
        <section className={css.section}>
                <h1>Do I potentially fit your open position?</h1>
                <p>Reach me at</p>
                <ul className={css.contactList}>
                    <li>
                        <LinkedInIcon />
                        <a href="https://www.linkedin.com/in/aaronccj-21534095/" target="_blank" rel="noreferrer">aaronccj-21534095</a>
                    </li>
                    <li>
                        <EmailIcon />
                        <a href="mailto:acmitr@gmail.com">acmitr@gmail.com</a>
                    </li>
                </ul>
        </section>
    );
}

export default Contact;