import React from 'react';

import SkillItemType from '../../../models/SkillItem';

import css from './SkillCard.module.css';

function SkillCard(props: {skill: SkillItemType}) {
    return (
        <div className={css.card}>
            <div className={css.header}>
                <h1 className={css.title}>{props.skill.name}</h1>
                <h2 className={css.tag}>{props.skill.tag}</h2>
            </div>
            <div className={css.content}>
                {props.skill.content}
            </div>
        </div>
        
    );
}

export default SkillCard;