import React from 'react';

import CheckIcon from '@mui/icons-material/Check';
import Button from '../Buttons/Button';
import LinkButton from '../Buttons/LinkButton';

import RoleItemType from '../../../models/RoleItem';

import css from './RoleCard.module.css';

function RoleCard(props: {roleData: RoleItemType, disabled?: boolean, disableMsg?: string, linkTo?: string }) {
    return (
        <div className={css.roleCard}>
            <div className={css.header}>
            <p className={css.title}>{props.roleData.title}</p>
            <p className={css.subtitle}>{props.roleData.subtitle}</p>
            </div>
            {/* <hr /> */}
            <div className={css.description}>
            <ul>
            {props.roleData.contentList.map((content, index) => {
                return (
                    <li key={index}>
                        <div className={css.roleDataListItem}>
                            <span>
                                <CheckIcon className={css.checkIcon} />
                            </span>
                            {content}
                        </div>
                    </li>
                );
            })}
            </ul>
            </div>
            {/* <hr /> */}
            <div className={css.more}>
                {props.disabled ? 
                    <Button title={props.disableMsg || ''} disabled>Out of service</Button> 
                    : <LinkButton to={props.linkTo || '/'}>More Info</LinkButton>
                }
            </div>
        </div>
    );
}

export default RoleCard;