import React from 'react';

// import Carousel from 'react-bootstrap/Carousel';

import css from './DemoCarousel.module.css'

export type CarouselItem = {
    image: { src: string, alt: string }
};

const DemoCarousel = (props: { id: string, items: CarouselItem[], pageInterval?: number }) => {
    const pageInterval = props.pageInterval || 5000;

    // react-bootstrap - not flexible
    // import Carousel from 'react-bootstrap/Carousel';
    //
    // return (
    //     <Carousel variant="dark" fade className={css.container}>
    //         {
    //             props.items.map((item: CarouselItem, index: number) => (
    //                 <Carousel.Item key={index}>
    //                     <img className={css.image} src={item.image.src} alt={item.image.alt} />
    //                     <Carousel.Caption>
    //                         <h3>{item.captions.title}</h3>
    //                         <p>{item.captions.content}</p>
    //                     </Carousel.Caption>
    //                 </Carousel.Item>
    //             ))
    //         }
    //     </Carousel>
    // );

    // legacy bootstrap
    return (
        <div id={props.id} className={`carousel slide ${css.container}`} data-bs-ride="carousel">
            <div className={`carousel-inner ${css.carouselInner}`}>
                {
                    props.items.map((item: CarouselItem, index: number) => (
                        <div key={index} className={`carousel-item container-fluid ${css.carouselItem} ${index === 0 && 'active'}`} data-bs-interval={pageInterval}>
                            <img className={css.image} src={item.image.src} alt={item.image.alt} />
                        </div>
                    ))
                }
            </div>

            <button className="carousel-control-prev" type="button" data-bs-target={`#${props.id}`} data-bs-slide="prev">
                <span className="carousel-control-prev-icon"></span>
            </button>
            <button className="carousel-control-next" type="button" data-bs-target={`#${props.id}`} data-bs-slide="next">
                <span className="carousel-control-next-icon"></span>
            </button>

            <div className="carousel-indicators">
                {
                    props.items.map((item: CarouselItem, index: number) => (
                        index === 0 ?
                            <button key={index} type="button" data-bs-target={`#${props.id}`} data-bs-slide-to={index} className="active" aria-current="true"></button>
                            : <button key={index} type="button" data-bs-target={`#${props.id}`} data-bs-slide-to={index}></button>
                    ))
                }
            </div>
        </div>
    );
};

export default DemoCarousel;