import React from 'react';

const SearchContext = React.createContext({
    keyword: '',
    keywordOnChange: (currentInput: string) => {}
});

export const SearchContextProvider = (props: {children: React.ReactNode}) => {

    const [inputKeyword, setInputKeyWord] = React.useState('');

    const keywordOnChangeHandler = (currentInput: string) => {
        // console.log(`context: ${currentInput}`);
        setInputKeyWord(currentInput); 
    };

    // React.useEffect(() => {
    //     console.log(`useEffect: inputKeyword=${inputKeyword}`);
    // }, [inputKeyword]);


    const context = {
        keyword: inputKeyword,
        keywordOnChange: keywordOnChangeHandler
    };

    return (
        <SearchContext.Provider value={context}>
            {props.children}
        </SearchContext.Provider>
    );
}

export default SearchContext;
